<template>
  <div class="home">
    <div style="text-align: left;">
      <input v-model="taskName" @keydown.enter="this.addTask(taskName);taskName=''">
      <button @click="this.addTask(taskName);taskName=''" style="cursor: pointer;">Add new task</button>
    </div>
      <h2 v-if="!tasks.length" :name="activeComponent">No tasks yet</h2>
      <TransitionGroup name="fade" mode="out-in">
      <p v-for="(task,i) in tasks" :key="task" class="task" draggable="true" :class="{finishedTask:task.finished}">
        <input type="checkbox" v-model="task.finished" @change="this.saveTasks()"/>
        {{ task.taskName }}
      <span style="text-decoration:solid"><button @click="this.deleteTask(i)" style="cursor: pointer; height: 25px;width:100%" >Delete Task</button></span>
      </p>
      </TransitionGroup>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'HomeView',
  components: {
  },
  data(){
    return {
      tasks:[
      ]
    }
  },
  methods:{
    addTask(task){
      if (task==undefined || task.trim()=='')return;
      this.tasks.push({finished:false,taskName:task})
      this.saveTasks()
    },
    deleteTask(i){
      this.tasks.splice(i,1)
      this.saveTasks()
    },
    saveTasks(){
      localStorage.setItem('tasks',JSON.stringify(this.tasks))
    }
  },
  mounted(){
    if(localStorage.getItem('tasks')){
      this.tasks=JSON.parse(localStorage.getItem('tasks'))
    }
  },
}
</script>
